    reviews: [
      {
        author_name: "Pritam Chanda",
        author_url: "https://www.google.com/maps/contrib/102015842501429447429/reviews",
        language: "en",
        profile_photo_url: "https://lh5.ggpht.com/-BEpEv3oqBqI/AAAAAAAAAAI/AAAAAAAAAAA/pHmk4TD03uk/s128-c0x00000000-cc-rp-mo/photo.jpg",
        rating: 1,
        relative_time_description: "a week ago",
        text: "I stay so far from AMRI and I use to do testing there. But once I asked by call whether my test report is ready or not, they said me. But what is the result, they said we couldn't say. Now in this pandemic situation, it is not easy to go hospital to take report for a person. They could easily let us know or send us report through email or WhatsApp.  I haven't liked it.",
        time: 1594818378,
      },
      {
        author_name: "Sukumar Ghosh",
        author_url: "https://www.google.com/maps/contrib/110390416814597233954/reviews",
        language: "en",
        profile_photo_url: "https://lh4.ggpht.com/-q3apxrXOeME/AAAAAAAAAAI/AAAAAAAAAAA/4UMZRt5JZsw/s128-c0x00000000-cc-rp-mo-ba6/photo.jpg",
        rating: 5,
        relative_time_description: "6 months ago",
        text: "You will feel a patient friendly environment in the Hospital .Role of the nursing staff is appreciative.\nMy niece was admitted in the hospital for appendix removal surgery.\nOperation was done by Dr D.K.Roy.\nHis gentle and polite behavior  helped improve confidence level of the \npatient. Sugery was done successfully.\nI convey my deep gratitude to doctor and others medical staff and the Hospital authority for their excellent service.\n.",
        time: 1579501569,
      },
      {
        author_name: "Sreeporna Dey",
        author_url: "https://www.google.com/maps/contrib/116079254849078104152/reviews",
        language: "en",
        profile_photo_url: "https://lh4.ggpht.com/-JjRgTsQCz7I/AAAAAAAAAAI/AAAAAAAAAAA/s8WqkWqmJRY/s128-c0x00000000-cc-rp-mo/photo.jpg",
        rating: 5,
        relative_time_description: "4 months ago",
        text: "I was recently admitted there for a critical surgery. I was late to reach there,but they took my admission immediately and without any delay proceeded me for the O.T. The doctors and medical team are excellent in their field & the operation was successful. The \nThe nursing staff are very much helpful and cooperative. They take good care of their patients. And the doctors are on round ups time to time. They never miss to visit any patient. Even one day I had severe stomach pain in middle of the night,but the nurse was immediately by my side, and the doctor arrived as soon as she was informed.They are always ready to help. Had a great experience there. Thankyou AMRI.",
        time: 1584092222,
      },
      {
        author_name: "Ujjwal Malik",
        author_url: "https://www.google.com/maps/contrib/113276544503643104798/reviews",
        language: "en",
        profile_photo_url: "https://lh3.ggpht.com/-XrwrOWfQdn8/AAAAAAAAAAI/AAAAAAAAAAA/rS2c5vNVmCo/s128-c0x00000000-cc-rp-mo-ba6/photo.jpg",
        rating: 5,
        relative_time_description: "a year ago",
        text: "Good and clean  hospital. I visit there for an surgery of my father. There treatment and services are good. They also provide good patient security. And support from experience medical team. I am very much satisfied with their support and good service of their stuff.",
        time: 1556617821,
      },
      {
        author_name: "Abhijit Das",
        author_url: "https://www.google.com/maps/contrib/109616542896982934422/reviews",
        language: "en",
        profile_photo_url: "https://lh3.ggpht.com/-Fado2UT05uE/AAAAAAAAAAI/AAAAAAAAAAA/PavWIQb7grU/s128-c0x00000000-cc-rp-mo-ba3/photo.jpg",
        rating: 5,
        relative_time_description: "4 months ago",
        text: "It's a good hospital. Treatment / operations. facilities are world  class. The staffs are cordial. Food (vegetarian) is quite tasty. I was admitted for gall stone removal through Lap Cholie. I am fully satisfied.",
        time: 1584201446,
      },
    ],
