        rating: 1,
        relative_time_description: "8 months ago",
        text: "Hospital campus is clean but  wards are still filthy.\n\nRamp for patient to take patients in the lift by wheelchair is available.\n\nDrinking water system near emergency doesn't work...so no drinking water available.\n\nPatients have to buy packaged drinking water bottles.\n\nVaccination ward security persons are so rude and not empathize with the patients.\n\nVaccines are available but doctor's are not.\nAnd due to the huge line the average waiting time for the patients is more than 3 hours (awful).\n\nIt's hospital where people get treated for various diseases but some processes need to change for the betterment.",
