        rating: 4,
        relative_time_description: "6 months ago",
        text: "Corporate Council for Leadership & Awareness (CCLA) is going to organize INDIA'S QUALITY EDUCATION AWARD 2018. During the auspicious award ceremony, a grand conclave will be organized with the aim to pay a humble salutation to educational institutions and persons who have made a commendable contribution to the realm of their corresponding domain respectively and have also served the nation in a progressive manner. In the PROGRESSIVE INDIA CONCLAVE 2018, eminent personalities from all over India will participate and discuss the USP behind the fame of their success. KPC  have been selected for INDIA'S QUALITY EDUCATION AWARD 2018",
        time: 1579382278,
