        language: "en",
        profile_photo_url: "https://lh4.ggpht.com/-KUjpzz_humo/AAAAAAAAAAI/AAAAAAAAAAA/uD_hbOwd6EQ/s128-c0x00000000-cc-rp-mo-ba6/photo.jpg",
        rating: 4,
        relative_time_description: "6 months ago",
        text: "Oldest medical college and hospital in West Bengal. This college produces finest doctors in the country. Students from other states also come here to study. Treatment in this hospital is very very good but as lots of patients come here so one have to keep patience when come in this hospital to visit the doctors. So please keep calm and take the benefits of best treatment.",
