    reviews: [
      {
        author_name: "Sumana Chowdhury",
        author_url: "https://www.google.com/maps/contrib/111152036641617930806/reviews",
        language: "en",
        profile_photo_url: "https://lh3.ggpht.com/-1qQgqYhlzT0/AAAAAAAAAAI/AAAAAAAAAAA/roRPOgdfPjQ/s128-c0x00000000-cc-rp-mo/photo.jpg",
        rating: 5,
        relative_time_description: "2 weeks ago",
        text: "I am very much satisfied with the services. All the service personnel were very much caring and courteous. But my husband had to wait for a long time at the time of discharge though TPA clearance reached in time. He got a call at 14.56 from the hospital for discharge, reached there at 15.25 and finally I could leave the hospital at 17.05.",
        time: 1594270460,
      },
      {
        author_name: "Sumit Bikram Maity",
        author_url: "https://www.google.com/maps/contrib/102793039107157672200/reviews",
        language: "en",
        profile_photo_url: "https://lh5.ggpht.com/-LLukDatRD0k/AAAAAAAAAAI/AAAAAAAAAAA/xDcvFIY5s94/s128-c0x00000000-cc-rp-mo-ba4/photo.jpg",
        rating: 4,
        relative_time_description: "a year ago",
        text: "A bit costly. Good infrastructure. Need to improve quality of food for patients admitted....!! Lastly nurses, staffs and doctors are very friendly. They put smiles on their face always..",
        time: 1559998708,
      },
      {
        author_name: "Sonukumar Sharma",
        author_url: "https://www.google.com/maps/contrib/102973404500884156829/reviews",
        language: "en",
        profile_photo_url: "https://lh6.ggpht.com/-29iRKF419xc/AAAAAAAAAAI/AAAAAAAAAAA/uukiSBq5T6A/s128-c0x00000000-cc-rp-mo/photo.jpg",
        rating: 1,
        relative_time_description: "in the last week",
        text: "giving fake corona positive reports. be careful.\nAfter leaving ship from haldia port we all 15 crew was sent for corona test, after 24 hours report came positive for 12 crew out of 15.\nwe all were surprised, since we directly came from ship in 5 different cars and also no body had a symptom, however we were put in quarantine, we all were in great depression, we planned to reconduct test from different hospital after 3 days, surprisingly report came all negative.\nthn we went to our respective home.",
        time: 1595344069,
      },
      {
        author_name: "madhurima ghosh",
        author_url: "https://www.google.com/maps/contrib/110311181532644726476/reviews",
        language: "en",
        profile_photo_url: "https://lh3.ggpht.com/-vIL5fq3aozk/AAAAAAAAAAI/AAAAAAAAAAA/n4gBdotN1ms/s128-c0x00000000-cc-rp-mo/photo.jpg",
        rating: 5,
        relative_time_description: "in the last week",
        text: "Very good service regarding OPD.Every time there stuff attending phone calls.Good Behavior also. Hope all your patients will get same service from them in this Pandemic situation.",
        time: 1595228108,
      },
      {
        author_name: "Sumanta Banerjee",
        author_url: "https://www.google.com/maps/contrib/104997493666005179877/reviews",
        language: "en",
        profile_photo_url: "https://lh5.ggpht.com/-paNChR3N5SI/AAAAAAAAAAI/AAAAAAAAAAA/0UocJJXG92Y/s128-c0x00000000-cc-rp-mo-ba4/photo.jpg",
        rating: 3,
        relative_time_description: "4 weeks ago",
        text: "I visited this place twice till now and I found All top facilities are available, Experienced Doctors and well behaved staffs. But one thing during this pandemic situation of Covid 19, the hospital is taking additional charge from their patients of Rs.  795 (for PPE kit) for their doctors safety in each pathology services and they are trying to establish that this is for the safety of the patients whereas patients are not allowed to wear that PPE. When I asked the reason to their top management, they said it's the hospital policy but neither it's put on the notice board, nor they communicated before billing and not even showed the policy on their official website. I strongly believe that the hospital must ensure the safety for its doctors, nurses and other staffs as they are doing business. If they can't do that, they must stop allowing patients to them.",
        time: 1593241903,
      },
    ],
