        text: "First of all they need to improve their management.. it's not syncing with their staffing....then low quality food, not very clean....not all doctors are bad some of them are very good actually... emergency ward needs improvement. In 4th floor ICU and HDU wards are good. 6th floor needs improvement though staff and nurses are very good. 7th floor is good as well. Most of it is very positive. But low response from the management. Even after admitting patient you don't have receive any sms or something. Price is pretty high.",
