        rating: 3,
        relative_time_description: "11 months ago",
        text: "A local hospital which caters to the patients of this region. Not the best hospital in terms of infrastructure and feel I'd say. Last time I entered their premises, the atmosphere was kind of dingy and dull. Not very encouraging to healing I must say. \n\nRecently an elderly neighbour of us got their pacemaker from this hospital, within 2 or so months they passed away after a week long suffering. We may never know what was the exact cause but I'd not be surprised if there was some issues with the pacemaker they installed. \n\nNot recommended until your other options are exhausted.",
