        rating: 1,
        relative_time_description: "6 months ago",
        text: "Plz plz don't visit this hospital if you want to keep your relatives better.  They are too expensive, everyone is running behind money and bills, treatment is nothing. Don't have ICU. No MRI SCAN machine. There is one horrible doctor name of F.. Atanu. I could hv slap him if my relatives not admitted. He don't know what is hospitality, even he don't hv basic manner to talk clients.",
