        rating: 1,
        relative_time_description: "9 months ago",
        text: "Never Ever visit this hospital . They only have money hunger . No responsibility , No  Care . Only billing & billing.... First Do a wrong treatment & then Rob more money from the side of Patient . So Pathetic & Disgusting Service . My Eldest brother almost got died for their so called 'best treatment' . After a successful court case , they have to give us our money back , Such a Shame to the authority .",
