        rating: 2,
        relative_time_description: "a year ago",
        text: "I have taken admission in Sanjiban University which is linked to Linkon University, Malaysia. Let's hope we could be a perfect Doctor.\nI have taken admission in last week of August and today is last week of October and yet council dont know the start of academic year. I think of being framed.",
