        rating: 4,
        relative_time_description: "2 years ago",
        text: "This ESI hospital is situated near the Nimdighi crossing on NH 16. The Uluberia rail station is also at around ten minutes walking distance from here. Thus, this place is well communicated as both road and rail facility available here.\n\nThis is the only ESI hospital in Uluberia subdivision. The people work in plants and factories can avail the facility of treatment for self and their families also. That too with a minimum or no cost at all.\n\nThe environment of this hospital is very serene. Some food stalls are also available near the entrance gate of the hospital. Sometimes, a large crowd might be seen at the outdoor section which may prove time consuming.\n\nAll over, the quality of treatment are supervised by the State Government of West Bengal at regular basis.",
