        relative_time_description: "6 months ago",
        text: "Very much satisfied with the staff behaviour  and service. They have opened  many counters which is impressive..It saves time. They coordinated very well. And most important part they care for old patients as well as handicapped  patients ..security staffs and housekeeping staffs are so much cooperative and active. Another important part is they use to provide online reports also. They supported  us at their end a lot..thanks to Naryana for being so helpful..just be like this and serve everyone.",
