        profile_photo_url: "https://lh5.ggpht.com/-5AL_96dFdUs/AAAAAAAAAAI/AAAAAAAAAAA/LzvNo3lC_Dg/s128-c0x00000000-cc-rp-mo-ba3/photo.jpg",
        rating: 3,
        relative_time_description: "10 months ago",
        text: "It's a new hospital with great infrastructure and situated in Phulbari Truck terminus away from the city. I found this hospital to be very neat clean and can be better in the near future. My mom was admitted here, on the internet it shows it's a full fledged running hospital with all the fecelities but I don't believe this is the fact. Incase of emergency, I would not recommend anyone to admit here. Although the hospital must be trying their best to match the service standard but the staff seem to be very new and not that organised. I came across few staff members and nurses to be very helpful but some were utterly dumb and indescipline with lack of skills. It can definitely be the best provided there are good specialists. This hospital has several temple and offers gust house to the ones admitted at a very subsidised rates. Overall experice during my five days stay, this hospital is ok ok and as I said it can definitely become the best.",
