    reviews: [
      {
        author_name: "Joydip Ghosh",
        author_url: "https://www.google.com/maps/contrib/113191654751365383547/reviews",
        language: "en",
        profile_photo_url: "https://lh4.ggpht.com/-U_kYgwEBGt4/AAAAAAAAAAI/AAAAAAAAAAA/lvKkZ_GPlMs/s128-c0x00000000-cc-rp-mo-ba4/photo.jpg",
        rating: 1,
        relative_time_description: "10 months ago",
        text: "The place is not less than a night mare, I paid 2,500Rs For every single day, In total they are charging me more than 4,500 Rs everyday. The service is extremely poor. I booked for Ac room Room is not clean for decades. I am staying in this hospital for last 4 days. Its feels they are taking my life, the pictures are the proof. They are using plastic plates not cleaned up properly on the other hand food are also worse quality. I dont even after I am cured from this stomach ache for which I will be carrying different virus or not because they using the same food plates for everyone or not.\nThe only reason I am here because of my Doctor he is very very good because he is old he treat people only near to his home. \nI am uploading some pictures as a proof, before you \ncome here double check it do you any other option or not.",
        time: 1567518246,
      },
      {
        author_name: "Byakuya Soumya",
        author_url: "https://www.google.com/maps/contrib/106076125523008864097/reviews",
        language: "en",
        profile_photo_url: "https://lh6.ggpht.com/-Q1Z-LUcKg90/AAAAAAAAAAI/AAAAAAAAAAA/dOkikgK_JI4/s128-c0x00000000-cc-rp-mo-ba3/photo.jpg",
        rating: 1,
        relative_time_description: "a month ago",
        text: "One of the worst private hospitals of Kolkata. They transfer patients who are already dead for over an hour to their ICCU just to show the relatives that they are trying whereas they just keep the patient there and act out some monkey business and declare the patient dead after an hour or two just to increase the billing amount of the party",
        time: 1592122822,
      },
      {
        author_name: "shuvankar Debroy",
        author_url: "https://www.google.com/maps/contrib/101461086128834658284/reviews",
        language: "en",
        profile_photo_url: "https://lh4.ggpht.com/-74PwEn59WgE/AAAAAAAAAAI/AAAAAAAAAAA/bhmSREukSpA/s128-c0x00000000-cc-rp-mo-ba2/photo.jpg",
        rating: 4,
        relative_time_description: "a year ago",
        text: "Really well maintained clean and low cost health organisations... good doctors...friendly sisters ....well staffs...",
        time: 1553130061,
      },
      {
        author_name: "Shovan Ghorai",
        author_url: "https://www.google.com/maps/contrib/112694324199839422629/reviews",
        language: "en",
        profile_photo_url: "https://lh4.ggpht.com/-Oxy1w1uTsZM/AAAAAAAAAAI/AAAAAAAAAAA/zx8ehKZHggE/s128-c0x00000000-cc-rp-mo-ba6/photo.jpg",
        rating: 2,
        relative_time_description: "a year ago",
        text: "Only good thing about this hospital is caring of patients by the nurse if you give them extra money. Good location. Located near V.I.P Laketown. Doctors do not come on the time. Not a good quality infrastructure though some facilities are available. Visiting hours with the patient, 10 to 11a.m and 4 to 6 p.m.",
        time: 1534101500,
      },
      {
        author_name: "Harshita Bajaj",
        author_url: "https://www.google.com/maps/contrib/101267191969363001294/reviews",
        language: "en",
        profile_photo_url: "https://lh5.ggpht.com/-tGadrH6CkDE/AAAAAAAAAAI/AAAAAAAAAAA/luBFu7cL2yE/s128-c0x00000000-cc-rp-mo/photo.jpg",
        rating: 1,
        relative_time_description: "5 months ago",
        text: "My mother was admitted to the hospital in the emergency section on 26th January because of the food poisoning and there was 2-3 nurses which knew nothing about Saline And NS. After trying for ten mins, they couldn't insert the injection properly which lead to the severe pain in her hands and also the blood clotting. it's been three days and her hand is fully swollen now. I will never recommend Dafodil Hospital. They don't know how to treat their patients in emergencies.",
        time: 1580283253,
      },
    ],
    user_ratings_total: 118,
