        rating: 1,
        relative_time_description: "9 months ago",
        text: "Experience was very bad. Nurse are untrained, the doctor who has done the Iv to my 9 month old its very bad, they will not allow anyone to see how they are doing it, they will tie the kid with belt and put pillows on their feet and them scared. Its breaks my heart. IF ARE GOING THERE TO TREAT YOUR BABY BE CAREFUL , TRY TO GET INSIDE WHERE THEY ARE DOING IV. \nThe nurse they are so bad you cant imagine. They are so careless you cant even imagine. \nMy baby boy was admitted in paediatric ward 4 Th floor. I have added some pictures. They will charge you high.",
