        text: "A hospital that will surely disappoint you. Nothing ever happens on time, be it the diagnostics or the doctors visit. The nurses are untrained, the staff rude and lying all the time about doctor's timings. With paying so much, you can avail the facility of any other hospital that will surely look after the mother and baby better.",
