        rating: 5,
        relative_time_description: "6 months ago",
        text: "Well maintained, reputed and old nursing home of barasat city. It is located on jessore road (national highway 112), near champadali more, beside barasat matsya aratdar samiti, near barasat fish market, near north 24 parganas district hospital, opposite government of west bengal pwd directorate office. \n\nThey appoint good qualified doctors, nurses, officials to give best facility to their patients. \nThey have well equipped intensive care unit, cctv survilance security system, 24hours heath care for children and adults. \n\nThey have well maintained operation theatre, patients cabin, emergency department, ambulance facilities, wheel chair facilities, 24x7 medicine stock, medical imaging facilities.\n\nMegacity nursing home is well communicated with neighbouring areas by different bus route, auto rickshaw route and electric rickshaw route. \nLocals believe them very much.",
